import { Component, OnInit } from '@angular/core';
import { LoadingController } from '@ionic/angular';
import { HttpService } from '../../api/http.service'
import { Router } from '@angular/router';
import { DomSanitizer } from '@angular/platform-browser';
@Component({
  selector: 'app-accommodation',
  templateUrl: './accommodation.page.html',
  styleUrls: ['./accommodation.page.scss'],
})
export class AccommodationPage implements OnInit {
  currentColor:any;
  loaderToShow: any;
  url:any;
  safeUrl:any;
  constructor(public sanitizer: DomSanitizer,public router: Router,public loadingController: LoadingController,public shared: HttpService) {
    this.currentColor = 'light';
    this.url = this.router.getCurrentNavigation().extras.state.acourl;
    this.getTrustedUrl(this.url);
   }

  ngOnInit() {
    this.shared.showLoading()
  }


  getTrustedUrl(url:any){ 
    this.safeUrl = this.sanitizer.bypassSecurityTrustResourceUrl(url);
   }


  onLoad(){
    this.shared.hideLoading()
  }


  ionViewWillLeave(){
    this.shared.hideLoading()
  }


}
